const slider = document.querySelector('[data-slider="grade"]');
let swiper1 = null;

const initGradeSlider = () => {
  if (!slider) return;

  
  const handleResize = () => {
    if (window.innerWidth <= 1024) {
      if (!swiper1) {
        swiper1 = new Swiper(slider, {
          slidesPerView: 'auto',
          spaceBetween: 0,
          navigation: {
            nextEl: '.swiper-button-next.grade__next',
            prevEl: '.swiper-button-prev.grade__prev',
          },
          
        });
      }
    } else {
      if (swiper1) {
        swiper1.destroy(true, true);
        swiper1 = null;
      }
    }
  };

  handleResize();

  window.addEventListener('resize', handleResize);
};

export { initGradeSlider };