const slider = document.querySelector('[data-slider="team"]');

const initInfoSlider = () => {
  if (!slider) {
    return;
  }

  const swiper = new Swiper(slider, {
    slidesPerView: 'auto',
    spaceBetween: 16,
    navigation: {
      nextEl: '.swiper-button-next.team__next',
      prevEl: '.swiper-button-prev.team__prev',
    },
  });
};

export {initInfoSlider};
