const slider = document.querySelector('[data-slider="story"]');

const initStorySlider = () => {
  if (!slider) {
    return;
  }

  const swiper = new Swiper(slider, {
    slidesPerView: 'auto',
    spaceBetween: 16,
    navigation: {
      nextEl: '.swiper-button-next.story__next',
      prevEl: '.swiper-button-prev.story__prev',
    },
  });
};

export {initStorySlider};